.aboutTitle {
  color: #a71814;
  text-align: center;
  margin-bottom: 0;
  font-size: 28px;
}

.underline {
  width: 240px;
  margin: auto;
  display: block;
}

.aboutDescription {
  color: #a71814;
  margin: 0 20px 0 20px;
}

.summary {
  font-weight: 700;
}

.imagesContainer {
  background-position-x: 100%;
  background-position-y: 93%;
  background-size: cover;
  position: relative;
}

.no-webp .imagesContainer {
  background-image: url("../../../../images/About/mobile/bg.png");
}

.webp .imagesContainer {
  background-image: url("../../../../images/About/mobile/bg.webp");
}

.pizzaSlice {
  width: 80%;
  display: block;
  margin: auto;
  padding-bottom: 40px;
}

.basil {
  position: absolute;
  width: 10%;
  left: 5%;
  top: 0;
}

.tomato {
  position: absolute;
  width: 12%;
  left: 18%;
}

.mushroom {
  position: absolute;
  width: 13%;
  bottom: 3%;
  right: 7%;
}

.mushroom2 {
  position: absolute;
  width: 13%;
  bottom: 20%;
  right: 1%;
}

@media (min-width: 640px) {
  .imagesContainer {
    background-position-y: 90%;
  }
  .aboutTitle {
    font-size: 40px;
  }
  .underline {
    width: 345px;
  }
  .aboutDescription {
    font-size: 24px;
  }
}
