.subpageMain {
  margin-top: 4.5em;
}

div.skos2 {
  position: relative;
  width: 100%;
  height: 120vw;
  background-image: url(./images/skos2.png);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 160%;
}

div.salamibox {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
}
div.salamibox img {
  width: 75%;
}

div.palmOilFree {
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  width: 15%;
}

div.ingredients img {
  display: block;
  position: absolute;
}
div.ingredients img.salamiIngredientsMainMobile {
  bottom: 7%;
  left: 8%;
  width: 85%;
}

@media (min-width: 320px) {
  div.skos2 {
    margin-top: 120%;
  }
}

@media (min-width: 360px) {
  div.skos2 {
    margin-top: 98%;
  }
}

@media (min-width: 375px) {
  .subpageMain {
    margin-top: 5em;
  }
}

@media (min-width: 411px) {
  div.skos2 {
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .subpageMain {
    margin-top: 6em;
  }
}

@media (min-width: 1024px) {
  .subpageMain {
    margin-top: 4em;
  }
  div.skos2 {
    width: 100%;
    height: 42vw;
    max-height: 810px;
    background-image: url(./images/skos2-desktop.png);
    background-repeat: no-repeat;
    background-size: cover;
  }

  div.skos2 img.skos2 {
    display: none;
  }
  div.salamibox {
    width: 55%;
    top: -19%;
    left: 18%;
  }
  div.ingredients {
    display: none;
  }
}
