div.skos3 {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

div.informations {
  padding: 10px;
  margin: 20% 5% 5%;
}
img.arrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arro-mobile.png");
}

.nutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.title {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.lineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/podkreslenie.png);
  margin-bottom: 15px;
}
div.lineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/podkreslenie.png);
}

div.line:nth-of-type(2) {
  width: 11vw;
}

div.nutritionalContainer:nth-child(2),
div.alergensContainer:nth-child(2) {
  margin-top: 2%;
}
div.nutritionalContainer,
div.alergensContainer {
  cursor: pointer;
}
div.nutritionalValue {
  display: flex;
}

div.nutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.alergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.lineInformations {
    width: 310px;
  }
  div.lineAlergens {
    width: 140px;
  }
  p.title {
    font-size: 35px;
  }
  div.nutritionalValue,
  .alergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.arrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.skos3 {
    display: block;
    height: 0;
    background-image: none;
  }
  div.informations {
    position: absolute;
    right: 5%;
    top: -700%;
    margin: 0;
    width: 20%;
  }
  p.title {
    font-size: 23px;
    color: #ba2323;
    font-weight: 700;
  }
  div.lineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/czerwone-podkreslenie.png);
    background-size: contain;
    background-position: center;
  }

  div.lineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/czerwone-podkreslenie.png);
    background-position: center;
  }
  .nutritionalValues {
    color: #ba2323;
    transition: 0.1s;
  }

  div.nutritionalValue,
  .alergensDescription {
    font-size: 12px;
  }

  .alergensDescription {
    color: #ba2323;
    display: block;
  }
}
