.guseppePullsInContainer {
  margin: 0 30px;
}

.guseppePullsInTitle {
  font-size: 28px;
  color: #a71814;
  text-align: center;
  margin: 0;
}

.guseppePullsInUnderline {
  width: 250px;
  margin: auto;
  display: block;
}

.guseppePullsInVegetables {
  display: block;
  margin: 40px auto;
  width: 40%;
}

.galleryContainer {
  display: flex;
  max-width: 640px;
  margin: 20px auto;
  justify-content: center;
  flex-wrap: wrap;
}

.instaPhotoContainer {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.instaPhotoContainer a {
  padding: 12px;
  margin: 0;
  box-sizing: border-box;
  flex: 1 0 50%;
}

.instaPhotoContainer img {
  width: 100%;
  display: block;
}
.instaPhotoContainerDesktop {
  display: none;
}

.instaPhotoContainerDesktop img {
  width: 100%;
}
.guseppePullsInInstagramIcon,
.guseppePullsInFacebookIcon {
  display: block;
  width: 60%;
}
.guseppePullsInFacebookIcon {
  width: 58%;
}

@media (min-width: 640px) {
  .guseppePullsInTitle {
    font-size: 40px;
  }
  .guseppePullsInUnderline {
    width: 360px;
    margin: auto;
    display: block;
  }
  .guseppePullsInVegetables {
    width: 35%;
  }
}

@media (min-width: 1024px) {
  .guseppePullsInContainer {
    position: relative;
    margin: 0 80px;
    margin-top: 60px;
  }
  .instaPhotoContainer a {
    flex: 1 0 25%;
  }
  .instaPhotoContainerDesktop a {
    display: block;
    width: 25%;
  }
  .guseppePullsInInstagramIcon {
    width: 25%;
    cursor: pointer;
  }
  .guseppePullsInFacebookIcon {
    width: 23%;
    cursor: pointer;
  }

  .guseppePullsInFacebookIcon:hover,
  .guseppePullsInInstagramIcon:hover {
    transition: 0.3s;
    filter: brightness(1.3);
  }

  .guseppePullsInVegetables {
    margin: 0;
    position: absolute;
    width: 16%;
    bottom: -22%;
    right: 5%;
  }
  .guseppePullsInTitle {
    font-size: 50px;
  }
  .guseppePullsInUnderline {
    width: 400px;
  }
  .galleryContainer {
    max-width: initial;
  }
}

@media (min-width: 1280px) {
  .guseppePullsInTitle {
    font-size: 62px;
  }
  .guseppePullsInUnderline {
    width: 500px;
  }
  .guseppePullsInContainer {
    margin-top: 100px;
  }
}

@media (min-width: 1440px) {
  .guseppePullsInVegetables {
    width: 16%;
    bottom: -16%;
    right: 10%;
  }
}
