.pizzaSliceContainer {
  width: 100%;
  background-position-y: 90%;
  background-position-x: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.consumerServiceUnderline {
  width: 290px;
  height: 12px;
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
}

.phoneUnderline {
  width: 140px;
  height: 12px;
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
}

.phoneNumberUnderline {
  width: 190px;
  height: 12px;
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
}

.contactContainer {
  position: relative;
}

.contactIntro {
  color: #a71814;
}

.address {
  font-weight: 600;
}

.address,
.email,
.phone,
.info {
  color: #a71814;
}

.copyright,
.info {
  color: #a71814;
  font-size: 12px;
}

.contactPizzaSlice {
  width: 75%;
  display: block;
  margin: auto;
}

.contactBasil {
  width: 15%;
  position: absolute;
  top: -2%;
  right: 30%;
}

.contactTomato {
  width: 15%;
  position: absolute;
  top: -1%;
  right: 40px;
}

.consumerService,
.phone,
.phoneNumber {
  font-weight: 600;
  font-size: 28px;
  color: #a71814;
  margin-bottom: 0px;
}
.phone {
  margin-top: 0;
}
.phoneNumber {
  margin-top: 0px;
  text-decoration: none;
}

@media (min-width: 540px) {
  .contactBasil {
    width: 10%;
    right: 20%;
  }

  .contactTomato {
    width: 12%;
    right: 40px;
  }
}

@media (min-width: 640px) {
  .phone,
  .consumerService,
  .phoneNumber {
    font-size: 40px;
  }

  .consumerServiceUnderline {
    width: 420px;
  }

  .phoneUnderline {
    width: 200px;
    height: 18px;
  }

  .phoneNumberUnderline {
    width: 270px;
    height: 18px;
  }

  .address,
  .email,
  .contactIntro {
    font-size: 24px;
    word-break: break-word;
  }
  .copyright,
  .info {
    font-size: 16px;
  }
  .contactBasil {
    top: -4%;
    width: 10%;
  }

  .contactTomato {
    width: 12%;
    top: -4%;
  }
  .contactPizzaSlice {
    width: 70%;
  }
}

@media (max-width: 280px) {
  .consumerServiceUnderline {
    width: 170px;
    height: 12px;
  }
}
