.burgerModal {
  width: 100%;
  height: calc(100vh - 4.5em);
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #a71814;
  z-index: 6;
  margin-top: 4.5em;
  align-items: center;
  overflow-y: scroll;
}

.burgerModal div {
  margin-bottom: 5%;
}

.burgerMenuPizzaContainer {
  flex-basis: 30%;
  text-align: center;
}

.burgerMenuPizzaContainer p {
  color: #fffad4;
  height: 32%;
  max-height: 32%;
  margin-top: 2px;
}

@media (min-height: 500px) {
  .burgerMenuPizzaContainer p {
    font-size: 14px;
  }
}

@media (min-height: 650px) {
  .burgerMenuPizzaContainer p {
    font-size: 15px;
  }
}

@media (min-width: 375px) {
  .burgerModal {
    height: calc(100vh - 5em);
    margin-top: 5em;
  }
}

@media (min-width: 768px) {
  .burgerModal {
    height: calc(100vh - 6em);
    margin-top: 6em;
  }
}

@media (min-width: 1024px) {
  .burgerModal {
    height: calc(100vh - 4em);
    margin-top: 4em;
  }
}
