.cookies {
  z-index: 9;

  width: 100vw;
  height: 100vh;

  position: fixed;
  left: 0;
  top: 0;

  background-color: #a71814;
  color: #fffad4;

  align-items: center;
  justify-content: center;

  display: flex;
  flex-direction: column;

}

.cookies__close-button {
  border: none;
  background: transparent;
  position: fixed;
  right: 12px;
  top: 12px;
  cursor: pointer;
}

.cookies__container {
  background-color: #a71814;
  height: 100%;
  width: 100%;
  z-index: 10;
  position: fixed;
  }

.cookies__content {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: calc(100% - 200px);
  max-height: 650px;
  margin: 30px 20px 0 20px;

  top: 0;
}

.cookies__content__header {
  margin-top: 0;
  font-size: 16px;
  font-weight: 600;
}

.cookies__content__text {
  font-size: 15px;
}

.cookies__content__hr {
  background-color: rgba($color: #fffad4, $alpha: 0.5);
  border: none;
  height: 2px;
  box-shadow: none;
  margin: 0;
}

.cookies_accept_all_btn {
  width: 100%;
  font-size: 16px;
  margin-top: 20px !important;
}

// EXPANDABLE
.cookies_expandable {
  vertical-align: middle;
}

.cookies_expandable_header {
  height: 50px;
  vertical-align: middle;
  line-height: normal;
  margin: 0;
  display: flex;
  align-items: center;
}

.cookies_expandable>* {}

.cookies_expandable_header>* {
  display: inline;
}

.cookies_expandable_header_text {}

// CHECKBOX
.cookies_expandable_checkbox {
  float: right;
}

.cookies_expandable_checkbox .tgl {
  display: none;
}

.cookies_expandable_checkbox .tgl,
.cookies_expandable_checkbox .tgl:after,
.cookies_expandable_checkbox .tgl:before,
.cookies_expandable_checkbox .tgl *,
.cookies_expandable_checkbox .tgl *:after,
.cookies_expandable_checkbox .tgl *:before,
.cookies_expandable_checkbox .tgl+.tgl-btn {
  box-sizing: border-box;
}

.cookies_expandable_checkbox .tgl::-moz-selection,
.cookies_expandable_checkbox .tgl:after::-moz-selection,
.cookies_expandable_checkbox .tgl:before::-moz-selection,
.cookies_expandable_checkbox .tgl *::-moz-selection,
.cookies_expandable_checkbox .tgl *:after::-moz-selection,
.cookies_expandable_checkbox .tgl *:before::-moz-selection,
.cookies_expandable_checkbox .tgl+.tgl-btn::-moz-selection,
.cookies_expandable_checkbox .tgl::selection,
.cookies_expandable_checkbox .tgl:after::selection,
.cookies_expandable_checkbox .tgl:before::selection,
.cookies_expandable_checkbox .tgl *::selection,
.cookies_expandable_checkbox .tgl *:after::selection,
.cookies_expandable_checkbox .tgl *:before::selection,
.cookies_expandable_checkbox .tgl+.tgl-btn::selection {
  background: none;
}

.cookies_expandable_checkbox .tgl+.tgl-btn {
  outline: 0;
  display: block;
  width: 3.3em;
  height: 1.8em;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.cookies_expandable_checkbox .tgl+.tgl-btn:after,
.cookies_expandable_checkbox .tgl+.tgl-btn:before {
  position: relative;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
}

.cookies_expandable_checkbox .tgl+.tgl-btn:after {
  left: 0;
}

.cookies_expandable_checkbox .tgl+.tgl-btn:before {
  display: none;
}

.cookies_expandable_checkbox .tgl:checked+.tgl-btn:after {
  left: 50%;
}

.cookies_expandable_checkbox .tgl-light+.tgl-btn {
  background: #a71814;
  border: 1px solid #fffad4;
  border-radius: 2em;
  padding: 2px;
  transition: all 0.4s ease;
}

.cookies_expandable_checkbox .tgl-light+.tgl-btn:after {
  border-radius: 50%;
  background: #fffad4;
  transition: all 0.2s ease;
}

.cookies_expandable_checkbox .tgl-light:checked+.tgl-btn {
  background: #fffad4;
  border: 1px solid #fffad4;
}

.cookies_expandable_checkbox .tgl-light:checked+.tgl-btn:after {
  background: #a71814;
}

.cookies_expandable_content {
  display: none;
  width: 100%;
  padding: 10px 0;
}

// EXPAMNDABLE BUTTON
.cookies_expandable_btn {
  background-color: transparent;
  border: none;
  color: #fffad4;
  font-weight: 800;
  font-size: 20px;
  margin-top: 1.25%;
  cursor: pointer;
}

//ACCEPT COOKIES BUTTON CONTAINER
.cokkies_button_container {
  position: sticky;
  bottom: 0px;
  height: 150px;
  margin: 0 20px;
  vertical-align: middle;
background-color: #a71814;}


@media (min-width: 1024px) {
  .cookies {
    max-width: 600px;
    max-height: 800px;
    height: 85vh;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
  }

  .cookies__container {
    padding: 20px 20px;
  }

  .cookies_content {
    height: calc(100% - 150px) !important;; 
  }

  .cookies_accept_all_btn {
    width: 200px;
    height: 35px;
    font-size: 15px !important;
    margin: 50px 0 0 0px !important;
  }

  .cokkies_button_container {
    position: relative;
    width: auto;
    height: 40px;
    display: flex;
    height: auto;

  }

  .cokkies_button_container .cookies_btn {
    height: 36px !important;
  }

  .cokkies_button_container .cookies_btn:first-child {
    width: 38% !important;
  }

  .cokkies_button_container .cookies_btn:last-child {
    width: 50% !important;
  }
}

.cookies_btn {
  align-items: center;
  justify-content: center;
  text-align: center;

  appearance: none;
  cursor: pointer;

  background-color: #fffad4;

  border-style: none;
  border-radius: 24px;
  color: #a71814;


  font-weight: 600;
  font-size: 14px;
  letter-spacing: 1px;

  padding: 7px 16px;
  margin: auto;

  height: 35px;
  max-height: 50px;

}

.cookies_btn_white {
  background-color: #fffad4;
  color: #a71814;
}

.cookies_btn_red {
  background-color: #a71814;
  border: 1px solid rgba($color: #fffad4, $alpha: 1.0);
  border-radius: 24px;
  color: #fffad4;
}

.cookies_btn_red_light {
  background-color: #a71814;
  border: 1px solid rgba($color: #fffad4, $alpha: 0.5);
  border-radius: 24px;
  color: #fffad4;
}

.cookiesInlineLink {
  color: #fffad4;
  text-decoration: underline;
  display: inline-block;
  padding-bottom: 2px;
}

.cookiesInlineLink:hover {
  cursor: pointer;
}

.cookies_short {
  width: 75vw;
  max-width: 1500px;
  height: 110px;
  background-color: #a71814;
  vertical-align: middle;
  justify-content: center;
  position: fixed;
  bottom: 5vh;
  z-index: 10;
  padding: 30px;
  left: 50%;
  transform: translateX(-50%);
  color: #fffad4;
}

.cookies_short_column {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.cookies_short_text {
  font-size: 13px;
}

.cookies_short_btn_column {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;

  max-width: 300px;
  margin: 0 auto;
}

.cookies_short_btn_column button {
  font-size: 13px;
  width: 100%;
}

#cookie_short_column_1 {
  width: 110px;
  margin: 0 20px 0 0;
}

#cookie_short_column_2 {
  width: 49%;
}

#cookie_short_column_2 p {
  height: 110px;
  display: table-cell;
  vertical-align: middle;
}

#cookie_short_column_3 {
  width: 34%;
  float: right;
}

#cookie_short_column_4 {
  display: none;
  width: 0px;
  margin: 0px;
  padding: 0px;
}


.cookies_floating {
  width: 4rem;
  height: 4rem;
  position: fixed;
  bottom: 2vw;
  left: 2vw;
  z-index: 10;
  cursor: pointer;
}

.cookies_floating:hover {
  animation: cookies_floating_hover_anim 1.5s linear infinite;
}

@keyframes cookies_floating_hover_anim {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }
}

@media (max-width: 1024px) {
  .cookies_short {
    width: 100vw;
    height: auto;
    bottom: 0;
    align-items: center;
    padding: 0px;
  }

  .cookies_short_text {
    font-size: 14px;
    top: 0;
    transform: none;
    padding: 0px;
  }

  #cookie_short_column_2 {
    width: auto;
    height: auto;
    float: none;
    margin: 35px 15px 0 15px;
  }

  #cookie_short_column_2 p {
    height: auto;
    line-height: 22px;
    display: table-cell;
    vertical-align: middle;
  }

  #cookie_short_column_3 {
    width: calc(100% - 30px);
    height: 100px;
    float: none;
    margin: 20px 15px 35px 15px;
    padding: 0px;
  }

  #cookie_short_column_4 {
    display: none; // Change to block if you want to use different buttons when on mobile
    width: calc(100% - 30px);
    height: 100px;
    float: none;
    margin: 20px 15px 35px 15px;
    padding: 0px;
  }

  .cookies_short_btn_column button {
    height: 40px;
    font-size: 15px;
  }

  #cookie_short_column_1 {
    display: none;
    width: 0px;
    padding: 0px;
    margin: 0px;
  }

  .cookies_short_btn_column {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .cookies_btn {
    height: 35px;
    max-height: 35px;
    font-size: 15px;
  }

  .cookies_floating {
    width: 3rem;
    height: 3rem;
  }
}