.moreSectionContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.howToBakeContainer,
.findShopContainer {
  margin: 0 25px;
  position: relative;
}

.findShop {
  width: 100%;
  display: block;
  margin: 10px auto;
}

.howToBake {
  width: 100%;
  display: block;
  margin: 0 auto 0;
}

.moreSectionVegetables {
  width: 32%;
  display: block;
  margin: 40px auto;
}

.searchIcon {
  position: absolute;
  z-index: 2;
  width: 36%;
  top: 59%;
  left: 11%;
}

.checkIcon {
  position: absolute;
  z-index: 2;
  width: 37%;
  top: 61%;
  left: 12%;
}

.findShopCircle {
  position: absolute;
  z-index: 2;
  width: 47%;
  top: 34%;
  left: 6%;
}

.howToBakeCircle {
  position: absolute;
  z-index: 2;
  width: 52%;
  top: 30%;
  left: 5%;
}

@media (min-width: 1024px) {
  .moreSectionContainer {
    flex-direction: row;
    position: relative;
    justify-content: center;
  }
  .findShop {
    width: 100%;
  }

  .howToBake {
    width: 100%;
  }
  .moreSectionVegetables {
    width: 16%;
    display: block;
    position: absolute;
    left: 5%;
  }

  .howToBakeContainer,
  .findShopContainer {
    flex-basis: 50%;
  }
  .howToBakeContainer {
    margin-right: 80px;
  }
  .findShopContainer {
    margin-left: 80px;
  }
  .checkIcon {
    top: 59%;
  }
}
