div.subpageMainBackground {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 74vh;
  min-height: 360px;
  z-index: 3;
  background-image: url(../images/skos.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: 120%;
}

.circleAnimationSubpage {
  display: none;
}

div img {
  width: 100%;
}

div.salami {
  text-align: center;
  position: absolute;
  top: 10%;
  width: 100%;
}
div.salami img {
  width: 65%;
}
div.pizza {
  margin: 0 auto;
  text-align: center;
  width: 90%;
  max-width: 620px;
}
div.pizza img {
  width: 80%;
}
div.circle {
  width: 50%;
  height: 20vw;
  position: absolute;
  top: 28%;
  right: 10%;
  background-image: url(../../../../common/images/Circle.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
div.button {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  align-items: center;
}
div.circle .buy {
  width: 100%;
}
div.circle .shop {
  margin-top: 10%;
  margin-right: 2%;
  width: 35%;
}
p.description {
  position: absolute;
  top: 100%;
  color: #ba2323;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding: 0px 40px 15px;
  margin-top: 80px;
}
span.bold {
  font-weight: 700;
}
div.ingredients_desktop {
  display: none;
}

.pizzaAnimation {
  margin: 0 auto;
  background-position-y: 0%;
  background-size: 100%;
  background-repeat: no-repeat;
  margin-left: auto;
}

@media (max-width: 1023px) {
  .outer {
    position: relative;
    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: (71 / 120) * 100%;
    }
    > .inner {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
}

@media (min-width: 320px) {
  div.subpageMainBackground {
    margin-bottom: 90%;
  }
}

@media (min-width: 360px) {
  p.description {
    padding: 15px 40px;
  }
}

@media (min-width: 500px) {
  p.description {
    font-size: 20px;
    margin-right: 5%;
    margin-left: 5%;
  }
}
@media (min-width: 550px) {
  p.description {
    font-size: 26px;
  }
}

@media (min-width: 760px) {
  p.description {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .pizzaAnimation {
    margin: 0;
    height: 100%;
    background-position-y: 0%;
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: auto;
  }

  div.subpageMainBackground {
    width: 100%;
    height: 58vw;
    max-height: 1110px;
    background-image: url(../images/skos-desktop.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 0;
  }

  div.circle {
    background-image: none;
  }

  .circleAnimationSubpage {
    display: block;
  }

  div.salami {
    text-align: left;
    top: 125px;
    margin-left: 8%;
  }
  div.salami img {
    width: 35%;
  }
  p.description {
    color: #fffad4;
    top: 25%;
    left: 0;
    font-size: 16px;
    width: 38%;
  }
  div.circle {
    top: 0;
    right: 5%;
    width: 23%;
    height: auto;
  }

  div.circle:hover {
    cursor: pointer;
  }

  div.button {
    transform: translate(-57%, -50%);
    width: 65%;
  }
  div.pizza {
    position: absolute;
    height: auto;
    bottom: 13%;
    right: 4%;
    width: 48%;
    max-width: 1080px;
  }
  div.pizza img {
    width: 45%;
  }
  div.ingredients_desktop {
    display: block;
  }
  div.ingredients_desktop img {
    display: block;
    position: absolute;
  }

  /* INGREDIENTS */
  div.ingredients_desktop img.desktop_salami {
    width: 10%;
    bottom: 12%;
    left: 20%;
  }
  div.ingredients_desktop img.desktop_chili {
    width: 10%;
    bottom: 10%;
    left: 8%;
  }
  /* CORNS INGREDIENTS */
  div.ingredients_desktop img.desktop_corn1,
  div.ingredients_desktop img.desktop_corn2,
  div.ingredients_desktop img.desktop_corn3 {
    width: 2.5%;
  }
  div.ingredients_desktop img.desktop_corn1 {
    bottom: 12%;
    left: 4%;
  }
  div.ingredients_desktop img.desktop_corn2 {
    bottom: 24%;
    left: 14%;
  }
  div.ingredients_desktop img.desktop_corn3 {
    bottom: 19%;
    left: 16%;
  }
  .outer {
    position: relative;
    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: (16 / 27) * 100%;
    }
    > .inner {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
}
@media (min-width: 1200px) {
  p.description {
    font-size: 20px;
  }
}
@media (min-width: 1365px) {
  p.description {
    font-size: 22px;
  }
}
@media (min-width: 1439px) {
  p.description {
    font-size: 24px;
  }
}
@media (min-width: 1599px) {
  p.description {
    font-size: 25px;
  }
}
@media (min-width: 1920px) {
  p.description {
    font-size: 32px;
  }
}
