.spotTitle {
  text-align: center;
  color: #a71814;
  margin-bottom: 0;
  font-size: 28px;
}

.spotTitleUnderline {
  width: 250px;
  display: block;
  margin: auto;
}

.videoContainer {
  position: relative;
  max-width: 600px;
  padding-top: 56.25%;
  margin: 0 auto;
  margin-top: 20px;
}

.video {
  position: absolute;
  top: 0;
  left: 0;
}

@media (min-width: 640px) {
  .spotTitle {
    font-size: 40px;
  }
  .spotTitleUnderline {
    width: 370px;
  }
}

@media (min-width: 1024px) {
  .spotTitle {
    margin-top: 100px;
    font-size: 50px;
  }
  .spotTitleUnderline {
    width: 420px;
  }
  .videoContainer {
    max-width: initial;
    padding-top: 40%;
    margin-left: 92px;
    margin-right: 92px;
  }
}

@media (min-width: 1280px) {
  .spotTitle {
    font-size: 62px;
  }
  .spotTitleUnderline {
    width: 500px;
  }
}
