.footerMenuItem {
  background-repeat: no-repeat;
  padding: 8px;
  height: 35px;
  min-width: 110px;
  text-align: center;
  color: #fffad4;
  cursor: pointer;
  margin-right: 5px;
  transition: 0.2s;
  transition-timing-function: linear;
  background-image: url("./images/footer-pizza-menu.png");
  display: flex;
  align-items: center;
  justify-content: center;
}

.footerMenuItem:hover {
  padding-top: 20px;
}

.slick-active:nth-child(1) .footerMenuItem,
.sliderDots li:nth-child(1) .footerMenuItem:hover {
  background-image: url("./images/ham-mushroom-selected.png");
}

.slick-active:nth-child(2) .footerMenuItem,
.sliderDots li:nth-child(2) .footerMenuItem:hover {
  background-image: url("./images/cheese-selected.png");
}

.slick-active:nth-child(3) .footerMenuItem,
.sliderDots li:nth-child(3) .footerMenuItem:hover {
  background-image: url("./images/salami-selected.png");
}

.slick-active:nth-child(4) .footerMenuItem,
.sliderDots li:nth-child(4) .footerMenuItem:hover {
  background-image: url("./images/Kebab-selected.png");
}

.slick-active:nth-child(5) .footerMenuItem,
.sliderDots li:nth-child(5) .footerMenuItem:hover {
  background-image: url("./images/chicken-curry-selected.png");
}

.slick-active:nth-child(6) .footerMenuItem,
.sliderDots li:nth-child(6) .footerMenuItem:hover {
  background-image: url("./images/ham-chorizo-selected.png");
}

.slick-active:nth-child(7) .footerMenuItem,
.sliderDots li:nth-child(7) .footerMenuItem:hover {
  background-image: url("./images/ham-and-salami-hot-selected.png");
}

.slick-active:nth-child(8) .footerMenuItem,
.sliderDots li:nth-child(8) .footerMenuItem:hover {
  background-image: url("./images/ham-garlic-sauce-selected.png");
}

.slick-active:nth-child(9) .footerMenuItem,
.sliderDots li:nth-child(9) .footerMenuItem:hover {
  background-image: url("./images/chicken-grilled-vegetables-selected.png");
}

.slick-active:nth-child(10) .footerMenuItem,
.sliderDots li:nth-child(10) .footerMenuItem:hover {
  background-image: url("./images/cheese-hot-selected.png");
}

.slick-active:nth-child(11) .footerMenuItem,
.sliderDots li:nth-child(11) .footerMenuItem:hover {
  background-image: url("./images/hawaii-selected.png");
}

.slick-active .footerMenuItem {
  padding-top: 20px;
}

@media (max-width: 1200px) {
  .footerMenuItem {
    min-width: 85px;
  }
}

@media (max-width: 1023px) {
  .footerMenuItem {
    display: none;
  }
}
