.contactContainerDesktop {
  display: flex;
  width: 100%;
  margin-top: 100px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.no-webp .contactContainerDesktop {
  background-image: url("../../../images/Footer/png/bg.png");
}
.webp .contactContainerDesktop {
  background-image: url("../../../images/Footer/bg.webp");
}

.consumerServiceDesktopUnderline {
  width: 420px;
}

.phoneDesktopUnderline {
  width: 450px;
}

.consumerServiceDesktop,
.phoneDesktop {
  font-weight: 600;
  margin-bottom: 0;
  font-size: 40px;
}

.contactInfoContainer,
.imagesContainerDesktop {
  flex-basis: 50%;
  color: #a71814;
  position: relative;
}

.contactInfoContainer {
  margin-left: 100px;
  margin-bottom: 200px;
}

.copyrightDesktop,
.infoDesktop {
  font-size: 12px;
}

.phoneDesktop {
  margin-top: 0px;
}
.contactPizzaSliceDesktop {
  width: 90%;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-40%);
}

.contactTomatoDesktop {
  width: 15%;
  display: block;
  position: absolute;
  right: 22%;
  top: 20%;
}
.contactBasilDesktop {
  width: 15%;
  display: block;
  position: absolute;
  right: 40%;
  top: 20%;
}

.addressDesktop {
  font-weight: 600;
}


.inlineLink {
  color: #a71814;
  text-decoration: none;
  display: inline-block;
  padding-bottom:2px;
  background-image: linear-gradient(#a71814 0 0);
  background-position: 0 100%; 
  background-size: 0% 2px;
  background-repeat: no-repeat;
  transition:
    background-size 0.3s,
    background-position 0s 0.3s; 
}

.inlineLink:hover {
  background-position: 100% 100%;
  background-size: 100% 2px;
  cursor: pointer;
}

@media (min-width: 1280px) {
  .contactIntroDesktop,
  .addressDesktop,
  .emailDesktop {
    font-size: 18px;
  }

  .copyrightDesktop,
  .infoDesktop {
    font-size: 14px;
  }

  .consumerServiceDesktopUnderline {
    width: 530px;
  }

  .phoneDesktopUnderline {
    width: 560px;
  }

  .phoneDesktop,
  .consumerServiceDesktop {
    font-size: 50px;
  }
  .contactTomatoDesktop {
    width: 12%;
    top: 15%;
    right: 30%;
  }
  .contactBasilDesktop {
    width: 12%;
    top: 15%;
    right: 45%;
  }
  .contactPizzaSliceDesktop {
    width: 90%;
    transform: translateY(-40%);
  }
  .contactContainerDesktop {
    background-position-x: 100%;
    background-position-y: 65%;
  }
}

@media (min-width: 1600px) {
  .contactIntroDesktop,
  .addressDesktop,
  .emailDesktop {
    font-size: 24px;
  }

  .contactInfoContainer {
    margin-bottom: 100px;
  }
}

@media (min-width: 1680px) {
  .contactTomatoDesktop {
    top: 0%;
  }
  .contactBasilDesktop {
    top: 0%;
  }
}
