.slider {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.nextButtonContainer,
.prevButtonContainer {
  flex-basis: 25%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.sliderImage {
  flex-basis: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: 0.3s;
}

.prevButton {
  background-color: transparent;
  cursor: pointer;
  border: none;
  transition: 0.1s;
  transition-timing-function: linear;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  outline: none;
}

.nextButton {
  left: 100%;
  background-color: transparent;
  cursor: pointer;
  border: none;
  transition: 0.1s;
  transition-timing-function: linear;
  position: absolute;
  top: 50%;
  z-index: 3;
  outline: none;
}

.nextButton:hover,
.prevButton:hover {
  filter: brightness(1.3);
}

.nextButton img,
.prevButton img {
  width: 60%;
}

img.ovalLineAnimation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  width: 125px;
  height: 70px;
  margin-top: 10px;
}
.slick-next:before,
.slick-prev:before {
  color: #000;
}

.sliderDots {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex !important;
  width: auto;
  list-style: none;
  justify-content: center;
  margin: 0;
  height: 80px;
  align-items: flex-end;
  padding: 0;
  z-index: 2;
  font-size: 13px;
}

.sliderContainer {
  height: calc(70vh - 4.5em);
  margin: 0 auto;
  padding: 0;
  width: 63%;
  position: relative;
  margin-top: auto;
  margin-bottom: auto;

  .choosePizzaMenu {
    position: fixed;
    bottom: 0;
    margin: 0 auto;
    right: 0;
    left: 0;
  }
}
.choosePizzaMenu button {
  background-color: transparent;
  border: none;
  color: #fffad4;
  font-size: 16px;
  font-weight: 700;
}

.sliderImg {
  width: 100%;
}

.sliderImgContainer {
  width: 75%;
  margin: auto;
  transform: "translate(0, 0)";
}

.choosePizzaMenu {
  display: none;
  z-index: 3;

  a,
  a:hover,
  a:focus,
  a:active {
    color: #ffffff;
    text-decoration: none;
  }
}

.slick-slide {
  overflow: hidden;
}

.sliderPizza {
  cursor: pointer;
  position: absolute;
  top: 1%;
  left: 14%;
  width: 70.5%;
  z-index: 0;
}

@media (max-width: 1600px) {
  .sliderDots {
    font-size: 13px;
  }
}

@media (max-width: 1500px) {
  .sliderContainer {
    width: 80%;
  }
}

@media (max-width: 1400px) {
  .sliderContainer {
    width: 85%;
  }
}

@media (max-width: 1365px) {
  .sliderDots {
    font-size: 12px;
  }
}

@media (max-width: 1200px) {
  .sliderContainer {
    width: 80%;
  }
}

@media (max-width: 1023px) {
  .sliderContainer {
    width: 100%;
  }
  .prevButton p,
  .ovalLineAnimation {
    display: none;
  }

  .nextButton p,
  .ovalLineAnimation {
    display: none;
  }
  .prevButton,
  .nextButton {
    width: 20%;
  }

  .choosePizzaMenu {
    height: 80px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: rgb(255, 250, 212);
    font-weight: 700;
    margin: 0 auto 20px;
  }
}

@media (max-width: 820px) {
  .sliderContainer {
    width: 100%;
  }

  .prevButton p {
    display: none;
  }

  .nextButton p {
    display: none;
  }
  .prevButton,
  .nextButton {
    width: 20%;
  }

  .sliderImg {
    width: 100%;
  }
  .sliderImgContainer {
    width: 90%;
  }
}

@media (max-width: 768px) {
  .slick-slider.slick-initialized {
    margin-top: 0%;
  }
}

@media (max-width: 415px) {
  .slick-slider.slick-initialized {
    margin-top: 5%;
  }
}

@media (min-width: 375px) {
  .sliderContainer {
    height: calc(70vh - 5em);
  }
}

@media (min-width: 768x) {
  .sliderContainer {
    height: calc(70vh - 6em);
  }
}

@media (min-width: 1024px) {
  .sliderImg {
    height: 100%;
    max-height: 100%;
  }
  .sliderImgContainer {
    width: 75%;
    max-width: 75%;
  }
  .sliderContainer {
    height: 100%;
  }
  .slick-slider {
    height: 100%;
  }
}
