.aboutContentContainer {
  display: flex;
  margin-top: 40px;
}

.imagesContainerDesktop {
  flex-grow: 1;
  display: flex;
  justify-content: flex-start;
  align-content: flex-end;
}

.aboutDescriptionDesktop {
  margin-left: 100px;
  color: #a71814;
  font-size: 16px;
}

.aboutTitleDesktop {
  text-align: center;
  color: #a71814;
  font-size: 50px;
  margin-bottom: 0;
}

.underlineDesktop {
  width: 400px;
  display: block;
  margin: 0 auto;
}

.pizzaSliceDesktop {
  width: 85%;
  height: 95%;
}

.basilDesktop {
  position: absolute;
  bottom: 10%;
  left: 90px;
  width: 5%;
}

.tomatoDesktop {
  position: absolute;
  bottom: 25px;
  left: 160px;
  width: 7%;
}

.mushroomDesktop {
  position: absolute;
  bottom: 4%;
  right: 6%;
  width: 8%;
}

.mushroom2Desktop {
  position: absolute;
  bottom: 17%;
  right: 2%;
  width: 8%;
}
.groupVegetablesDesktop {
  position: absolute;
  top: 4%;
  right: 8%;
  width: 14%;
}

@media (min-width: 1280px) {
  .aboutTitleDesktop {
    font-size: 62px;
  }
  .aboutDescriptionDesktop {
    font-size: 18px;
  }
  .basilDesktop {
    bottom: 10%;
    width: 6%;
  }

  .tomatoDesktop {
    bottom: 50px;
    left: 15%;
    width: 8%;
  }
}

@media (min-width: 1600px) {
  .aboutDescriptionDesktop {
    font-size: 24px;
  }

  .underlineDesktop {
    width: 500px;
  }
  .tomatoDesktop {
    bottom: 50px;
    left: 13%;
    width: 8%;
  }
}
