.header {
  height: 4.5em;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 4;
  background-color: #fffad4;
}
.popover-body {
  padding: 0.5rem 0.75rem;
  color: #212529;
}

.popover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1060;
  display: block;
  max-width: 276px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  text-align: left;
  text-align: start;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-break: normal;
  word-spacing: normal;
  white-space: normal;
  line-break: auto;
  font-size: 0.875rem;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  margin-top: 0.5rem;
}

.popover .arrow {
  position: absolute;
  display: block;
  width: 1rem;
  height: 0.5rem;
  margin: 0 0.3rem;
}

.bs-popover-bottom > .arrow,
.bs-popover-auto[x-placement^="bottom"] > .arrow {
  top: calc(-0.5rem - 1px);
}

.popover .arrow::before,
.popover .arrow::after {
  position: absolute;
  display: block;
  content: "";
  border-color: transparent;
  border-style: solid;
}

.bs-popover-bottom > .arrow::after,
.bs-popover-auto[x-placement^="bottom"] > .arrow::after {
  top: 1px;
  border-width: 0 0.5rem 0.5rem 0.5rem;
  border-bottom-color: #fff;
}
.arrow::after {
  border-bottom-color: #a71814 !important;
}

.logoContainer {
  position: relative;
  flex-basis: 50%;
}

.logoDrOetker {
  width: 35%;
  margin: 8px;
  z-index: 8;
  position: relative;
}

.logoGuseppe {
  position: absolute;
  top: 40%;
  left: 0;
  z-index: 7;
  width: 100%;
  cursor: pointer;
}

.navigation {
  flex-grow: 1;
  display: flex;
}

.socialMedia {
  height: 100%;
  flex-basis: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 3px;
}

a {
  display: block;
  margin-right: 2px;
}

.socialMedia a img {
  width: 75%;
}

.FacebookButton {
  margin-right: 4px;
}

.FacebookButton:hover,
.MessangerButton:hover,
.InstagramButton:hover,
.YoutubeButton:hover {
  filter: brightness(1.4);
  transition: 0.3s;
  transition-timing-function: linear;
  cursor: pointer;
}

.burgerMenu {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.burgerMenu img {
  width: 40%;
  margin: 1px;
}

.menu {
  flex-basis: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu button {
  background-color: transparent;
  border: none;
  width: 80%;
  height: 80%;
}

.navigationElementImage {
  display: block;
  width: 0;
  background-color: #a71814;
  border: 0px solid #a71814;
  border-radius: 5px;
  height: 2px;
  bottom: 20%;
  position: absolute;
  transition: 0.2s;
  transition-timing-function: linear;
}

.burgerNavigation {
  list-style: none;
  color: #fffad4;
  text-align: center;
  padding: 0;
  margin: 50px 0px;
  font-size: 16px;
}

@media (min-width: 320px) {
  .logoDrOetker {
    width: 25%;
  }

  .logoGuseppe {
    top: 20%;
    width: 100%;
  }
}

@media (min-width: 375px) {
  .header {
    height: 5em;
  }
  .logoGuseppe {
    top: 15%;
  }
}

@media (min-width: 540px) {
  .logoDrOetker {
    width: 18%;
  }

  .logoGuseppe {
    top: 10%;
    width: 80%;
  }
}

@media (min-width: 640px) {
  .burgerNavigation li {
    font-size: 24px;
  }
  .burgerMenu {
    height: 65px;
  }
}

@media (min-width: 768px) {
  .header {
    height: 6em;
  }
  .logoDrOetker {
    width: 15%;
  }

  .logoGuseppe {
    top: 10%;
    width: 70%;
  }
  .burgerMenu img {
    width: 35%;
  }
}

@media (min-width: 900px) {
  .burgerMenu img {
    width: 25%;
  }
}

// PC
.navigationList {
  flex-basis: 80%;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  color: rgb(167, 24, 20);
  font-weight: 700;
  font-size: 12px;
  margin: 0;
  align-content: center;
}

@media (min-width: 1200px) {
  .header {
    height: 4em;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 4;
    background-color: #fffad4;
  }

  .logoContainer {
    margin: auto;
    flex-basis: 30%;
  }

  .logoDrOetker {
    width: 60px;
    margin-left: 26%;
  }

  .logoGuseppe {
    top: 0;
    left: 26%;
    width: 300px;
  }
  .socialMedia {
    justify-content: flex-start;
    flex-basis: 20%;
    align-items: center;
    margin-left: 10px;
  }

  a {
    margin-left: 0px;
  }
  a img {
    width: 70%;
  }

  .navigationElement {
    margin-right: 8px;
    text-decoration: none !important;
    color: inherit !important;
    display: block;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: relative;
  }

  .navigationElement:hover .navigationElementImage {
    width: 100%;
    border: 1px solid #a71814;
  }
}

@media (min-width: 1366px) {
  .navigationList {
    font-size: 14px;
  }

  a {
    text-align: center;
  }
}

@media (min-width: 1600px) {
  .navigationList {
    font-size: 16px;
  }
}
