.productsModal {
  position: fixed;
  top: 4em;
  left: 0;
  width: 100%;
  height: calc(100vh - 4em);
  background-color: #a71814;
  z-index: 6;
}

.productsContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: auto;
  margin-top: 120px;
  margin-bottom: 50px;
  height: calc(100% - 120px);
  max-height: calc(100% - 120px);
  color: #fffad4;
  width: 95%;
}

.pizzaContainer {
  flex-basis: 20%;
  height: 30%;
  cursor: pointer;
  text-align: center;
  margin-top: 5px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.pizzaContainer p {
  font-size: 20px;
  height: 32%;
  max-height: 32%;
}

@media (min-width: 1024px) {
  a.pizzaContainer p::after {
    display: block;
    content: "";
    border-bottom: solid 4px #fffad4;
    border-radius: 5px 5px;
    transform: scaleX(0);
    transition: transform 250ms linear;
    transform-origin: 0% 50%;
    transition: 0.2s;
  }

  a.pizzaContainer:hover p::after {
    transform: scaleX(1);
    transform-origin: 0% 50%;
  }
}
