section.instruction_desktop {
  display: none;
}
@media (min-width: 1024px) {
  div.instructionStep1Container_desktop {
    width: 3.5%;
    height: 23%;
    margin-top: 15px;
    margin-right: 10px;
  }

  div.instructionStep2Container_desktop {
    width: 8%;
    height: 23%;
    margin-top: 15px;
    margin-right: 10px;
  }

  div.instructionStep3Container_desktop {
    width: 7%;
    height: 23%;
    margin-top: 15px;
    margin-right: 10px;
  }

  div.instructionStep1Container_desktop img,
  div.instructionStep2Container_desktop img,
  div.instructionStep3Container_desktop img {
    width: 100%;
  }

  section.instruction_desktop {
    margin: 0 5% 200px;
    display: block;
  }
  div.steps_desktop {
    display: flex;
    justify-content: space-between;
  }

  h2.instructionTitle_desktop {
    font-size: 26px;
    color: #ba2323;
    margin-bottom: 5px;
    font-weight: 600;
    margin-top: 0;
  }
  div.line_desktop {
    margin-top: -0.5%;
    background-image: url(../images/line.png);
    height: 15px;
    width: 260px;
    background-repeat: no-repeat;
    background-size: contain;
  }

  div.step_desktop1,
  div.step_desktop2,
  div.step_desktop3 {
    display: flex;
    justify-content: flex-start;
    align-content: center;
    margin-left: 10px;
  }

  div.step_desktop1 {
    flex-basis: 30%;
    height: 20%;
  }

  div.step_desktop2 {
    flex-basis: 30%;
    height: 20%;
  }

  div.step_desktop3 {
    flex-basis: 35%;
  }

  p.instruction_desktop {
    margin-top: 10px;
    font-size: 16px;
    color: #ba2323;
  }

  div.step_desktop3 p.instruction_desktop {
    min-width: 220px;
  }

  div.iconClock_desktop {
    width: 23%;
    margin: 10px 10px;
  }

  div.iconOven_desktop {
    width: 20%;
    margin: 10px 10px;
  }

  div.temperatureIcon_desktop {
    width: 32%;
    margin: 10px 5px;
  }
}

@media (min-width: 1280px) {
  p.instruction_desktop {
    font-size: 18px;
  }
}

@media (min-width: 1600px) {
  div.instructionStep1Container_desktop,
  div.instructionStep2Container_desktop,
  div.instructionStep3Container_desktop {
    margin-top: 17px;
  }
  h2.instructionTitle_desktop {
    font-size: 40px;
  }
  div.line_desktop {
    height: 23px;
    width: 400px;
  }

  div.temperatureIcon_desktop {
    width: 30%;
  }

  p.instruction_desktop {
    font-size: 22px;
  }
}
