.gameContainer {
  text-align: center;
}

.mobilegGameContainer {
  margin: 4.5em 0 0 0;
}

.game {
  height: calc(90vh - 4.5em);
  width: 100%;
  margin: 0;
}

.mobileGame {
  height: calc(100vh - 5.5em);
  width: 100%;
  margin: 0;
}

@media (min-width: 375px) {
  .game {
    height: calc(90vh - 5em);
  }
  .mobileGame {
    height: calc(100vh - 6em);
  }
  .mobilegGameContainer {
    margin-top: 5em;
  }
}

@media (min-width: 768px) {
  .game {
    height: calc(90vh - 6em);
  }
  .mobileGame {
    height: calc(95vh - 7em);
  }
  .mobilegGameContainer {
    margin-top: 6em;
  }
}

@media (min-width: 1024px) {
  .gameContainer {

  }

  .game {
    height: calc(100vh - 4em);
  }
}
