div.ingredients2 img {
  display: block;
  position: absolute;
}
div.ingredients2 img.salami {
  width: 20%;
  top: 50px;
  right: 5%;
}
div.ingredients2 img.corn1,
div.ingredients2 img.corn2,
div.ingredients2 img.corn3,
div.ingredients2 img.corn4 {
  width: 7%;
}
div.ingredients2 img.corn1 {
  top: 35px;
  right: 29%;
}
div.ingredients2 img.corn2 {
  top: 5px;
  right: 22%;
}
div.ingredients2 img.corn3 {
  top: 25px;
  right: 20%;
}
div.ingredients2 img.corn4 {
  top: 25px;
  right: 10%;
}
div.ingredients2 img.chilis {
  width: 15%;
  top: -5px;
  right: 1%;
}

@media (min-width: 540px) {
  div.ingredients2 img.salami {
    top: 120px;
  }
  div.ingredients2 img.corn1 {
    top: 70px;
  }
  div.ingredients2 img.corn2 {
    top: 30px;
  }
  div.ingredients2 img.corn3 {
    top: 65px;
  }
  div.ingredients2 img.corn4 {
    top: 60px;
  }
}

@media (min-width: 1024px) {
  div.ingredients2 {
    display: none;
  }
}
