@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap");

.instructionTitle {
  color: #ba2323;
  font-size: 20px;
  margin-bottom: 5px;
}
section.instruction {
  padding: 10px;
  margin: 10px 5%;
}
div.line {
  text-align: center;
  background-image: url(./images/line.png);
  width: 100%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}

div.step1,
div.step2,
div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #ba2323;
}

span.step {
  font-size: 30px;
  margin-right: 10px;
  font-weight: bold;
}
p.instruction {
  margin-top: 10px;
}
div.iconOven {
  display: flex;
  align-items: center;
  width: 50%;
}

div.temperatureIcon {
  display: flex;
  align-items: center;
  width: 30%;
}

div.iconClock {
  display: flex;
  align-items: center;
  width: 75%;
}

div.instructionStep1Container {
  width: 3.5%;
  height: 23%;
  margin-top: 4%;
  margin-right: 5px;
  margin-left: 7px;
}
div.instructionStep2Container {
  width: 16%;
  height: 23%;
  margin-top: 4%;
  margin-right: 5px;
}
div.instructionStep3Container {
  width: 18%;
  height: 23%;
  margin-top: 4%;
  margin-right: 5px;
}

div.instructionStep1Container img,
div.instructionStep2Container img,
div.instructionStep3Container img {
  width: 100%;
}

@media (min-width: 540px) {
  div.iconOven {
    width: 25%;
  }

  div.temperatureIcon {
    width: 22%;
  }
  div.iconClock {
    width: 38%;
  }
}

@media (min-width: 600px) {
  .instructionTitle {
    font-size: 35px;
  }
  div.line {
    height: 20px;
  }
  p.instruction {
    font-size: 25px;
  }
  div.instructionStep1Container {
    width: 2.5%;
    margin-top: 3%;
  }
  div.instructionStep2Container {
    width: 9%;
    margin-top: 3%;
  }
  div.instructionStep3Container {
    width: 10%;
    margin-top: 3%;
  }
}
@media (min-width: 1023px) {
  section.instruction {
    display: none;
  }
}
